<template>
  <div id="app">
    <NavBar/>

      <router-view />

  </div>
</template>

<script>
  import NavBar from "./components/NavBar";
  export default {
    components: {NavBar}
  }
</script>


<style lang="sass">
  @import "~materialize-css/dist/css/materialize.min.css"

  *
    margin: 0
    padding: 0
    box-sizing: border-box

  body
    margin: 0 auto
    overflow-x: hidden

  h1
    font-size: 2.5rem
    font-weight: normal
    color: #444
    text-align: center
    margin: 2rem 0
    line-height: 110%

  ::-webkit-scrollbar  /* 1 - скроллбар */
    width: 0
    height: 50px
    background-color: #FFFFFF

  /*::-webkit-scrollbar-button { !* 2 - кнопка *!*/
  /*}*/
  /*::-webkit-scrollbar-track { !* 3 - трек *!*/
  /*}*/
  /*::-webkit-scrollbar-track-piece { !* 4 - видимая часть трека *!*/
  /*}*/
  ::-webkit-scrollbar-thumb  /* 5 - ползунок */
    border-radius: 2px
    background-color: #d1d1d1

  /*::-webkit-scrollbar-corner { !* 6 - уголок *!*/
  /*}*/
  /*::-webkit-resizer { !* 7 - изменение размеров *!*/
  /*}*/

  @keyframes move_wave
    0%
      transform: translateX(0) translateZ(0) scaleY(1)

    50%
      transform: translateX(-25%) translateZ(0) scaleY(0.55)

    100%
      transform: translateX(-50%) translateZ(0) scaleY(1)


  .waveWrapper
    overflow: hidden
    position: absolute
    left: 0
    right: 0
    bottom: 0
    top: -1px
    margin: auto

  .waveWrapperInner
    position: absolute
    width: 100%
    overflow: hidden
    height: 100%
    bottom: -1px
    background-image: linear-gradient(to top, #86377b 20%, #27273c 80%)

  .bgTop
    z-index: 15
    opacity: 0.5

  .bgMiddle
    z-index: 10
    opacity: 0.75

  .bgBottom
    z-index: 5

  .wave
    position: absolute
    left: 0
    width: 200%
    height: 100%
    background-repeat: repeat no-repeat
    background-position: 0 bottom
    transform-origin: center bottom

  .waveTop
    background-size: 50% 100px

  .waveAnimation .waveTop
    animation: move-wave 3s
    -webkit-animation: move-wave 3s
    -webkit-animation-delay: 1s
    animation-delay: 1s

  .waveMiddle
    background-size: 50% 120px

  .waveAnimation .waveMiddle
    animation: move_wave 10s linear infinite

  .waveBottom
    background-size: 50% 100px

  .waveAnimation .waveBottom
    animation: move_wave 15s linear infinite

